import React, { useState } from "react";
import styles from "./index.module.scss";

const DarkButton = ({ title, dark, handleClick }) => {
    const [isMouse, setMouse] = useState(false);

    const getArrowIcon = () => {
        if (dark && isMouse) {
            return `https://egorbik.github.io/maindo/assets/img/arrowRightLight.png`;
        } else if (dark && !isMouse) {
            return `https://egorbik.github.io/maindo/assets/img/arrowRightLight.png`;
        } else if (!dark && isMouse) {
            return `https://egorbik.github.io/maindo/assets/img/arrowRightLight.png`;
        } else if (!dark && !isMouse) {
            return `https://egorbik.github.io/maindo/assets/img/arrowRightLight.png`;
        }
    };

    return (
        <>
            <button
                onClick={handleClick}
                onMouseEnter={() => {
                    setTimeout(() => {
                        setMouse(true);
                    }, 200);
                }}
                onMouseLeave={() => {
                    setTimeout(() => {
                        setMouse(false);
                    }, 300);
                }}
                className={styles.connectBtn}
            >
                <div className={styles.connectText}>{title}</div>
                <img className={styles.connectImg} src={getArrowIcon()} />
            </button>
            {/* <button class="grow_ellipse">GROW ELLIPSE</button> */}
        </>
    );
};

export default DarkButton;
